<template>
  <!--  -->
  <div class="c_lyt_main">
    <h3 class="lm_title"></h3>
    <div class="lm_sub_title"></div>
    <!--  -->
    <div class="lyt_address">
      <div class="addr_wrap">
        <!--  -->
        <div class="addr_item" v-for="(item,index) in addressArr" :class="{default:index==addrDef}" @click="choiceAddr(index)">
          <h3>{{item.region}} <span>({{item.name}})</span></h3>
          <p class="addr_deil">{{item.address}}</p>
          <p class="addr_btns"><span class="addr_edit" @click="editAddress"></span><span></span></p>
        </div>
        <div class="addr_item addr_insert" @click="showAddAddress">
          <p><i></i></p>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="lyt_uc_form" v-show="isShowAddAddress">
      <div class="ucf_title">{{addressTitle}}</div>
      <div class="ucf_main">
        <div class="ucf_form">
          <form @submit.prevent="insertAddressSubmit">
            <p class="fm_item">
              <label></label>
              <input type="text" name="username" v-model="username" v-validate="'required|username'">
              <span v-show="errors.has('username')" class="help is-danger">{{ errors.first('username') }}</span>
            </p>
            <p class="fm_item">
              <label></label>
              <input type="text" name="phone" v-model="phone" v-validate="'required|phone'">
              <span v-show="errors.has('phone')" class="help is-danger">{{ errors.first('phone') }}</span>
            </p>
            <p class="fm_item">
              <label></label>
              <input type="text" name="address" v-model="address" @focus="choiceAreas" v-validate="'required'">
              <span v-show="errors.has('address')" class="help is-danger">{{ errors.first('address') }}</span>
            </p>
            <p class="fm_item">
              <label></label>
              <input type="text" name="detailaddr" v-model="detailaddr" v-validate="'required'">
              <span v-show="errors.has('detailaddr')" class="help is-danger">{{ errors.first('detailaddr') }}</span>
            </p>
            <p class="fm_btns">
              <button type="submit" class="btn_save"></button>
              <button type="button" class="btn_cancel" @click="showAddAddress"></button>
            </p>
          </form>
        </div>
      </div>
    </div>
    <areas :isShow="isShowAreas" @onAreasOk="onAreasOk" @onAreasCancel="onAreasCancel"></areas>
    <!--  -->
    <div class="lm_sub_title"></div>
    <div class="lyt_table lyt_order">
      <table>
        <thead>
        <tr>
          <th width="40%"></th>
          <th width="15%"></th>
          <th width="15%"></th>
          <th width="15%"></th>
          <th width="15%"></th>
        </tr>
        </thead>
        <tbody>
        <tr>
          <td class="odr_detail">
            <img src="../../assets/icon_layout.png" class="pd_img">
            <span><i>  A     1.3 </i></span>
          </td>
          <td> 100CM</td>
          <td><i class="clr_rd">289.00</i></td>
          <td>2</td>
          <td>
            <i class="clr_rd">599.00</i>
          </td>
        </tr>
        <tr class="odr_dtl_bom">
          <td class="odr_total" colspan="5">
            <p><i class="clr_rd">188.00</i></p>
            <p>  </p>
            <p> 15399990000</p>
            <a href="#" class="submit_order"></a>
          </td>
        </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>
<script>
  import Areas from '../../components/areas.vue'
  export default {
    data(){
      return {
        isShowAddAddress: false,
        isShowAreas: false,
        addressTitle: '',
        username: '',
        phone: '',
        address: '',
        detailaddr: '',
        addressArr: [
          {
            id: 1,
            name: '',
            region:'  ',
            address: '1251701'
          },
          {
            id: 2,
            name: '',
            region:'  ',
            address: '1251701'
          }
        ],
        addrDef: 0
      }
    },
    methods: {
      /*  */
      showAddAddress(){
        this.addressTitle = '';
        this.isShowAddAddress = !this.isShowAddAddress;
      },
      /*  */
      editAddress(){
        this.addressTitle = '';
        this.isShowAddAddress = !this.isShowAddAddress;
      },
      /*  */
      insertAddressSubmit(){
        this.$validator.validateAll().then(() => {
          console.log('From Submitted!');
        }).catch(() => {
          console.log('Correct them errors!');
        });
      },
      /*  */
      choiceAreas(){
        this.isShowAreas = true;
      },
      /*  */
      onAreasOk(result){
        this.address = result.name;
        this.isShowAreas = false;
        this.$validator.errorBag.remove('address')
      },
      /*  */
      onAreasCancel(result){
        this.isShowAreas = result;
      },
      choiceAddr(index){
        this.addrDef = index;
      }
    },
    components: {
      Areas
    }
  }
</script>
<style lang="scss">
  @import "../../styles/common/lyt_uc_main.css";
  @import "../../styles/common/lyt_table.css";
  @import "../../styles/common/lyt_table_order.css";
  @import "../../styles/common/lyt_uc_form.css";
  @import "../../styles/common/lyt_address.css";
</style>



