<template>
  <!--  -->
  <div class="c_r lyt_login_regist">
    <div class="lr_bg"></div>
    <div class="lr_main">
      <div class="lr_cont">
        <h1 class="lr_logo"><img src="../../assets/logo.png"></h1>
        <form @submit.prevent="loginSubmit" class="lyt_form">
          <div class="fm_item">
            <input type="text" name="username" v-model="username" v-validate="'required|username'" placeholder="">
            <div class="fm_errs">
              <span v-show="errors.has('username')" class="help is-danger">{{ errors.first('username') }}</span>
            </div>
          </div>
          <div class="fm_item">
            <input type="text" name="password" v-model="password" v-validate="'required|min:6|username'" placeholder="">
            <div class="fm_errs">
              <span v-show="errors.has('password')" class="help is-danger">{{ errors.first('password') }}</span>
            </div>
          </div>
          <div class="fm_item half">
            <input type="text" name="verifycode" v-model="verifycode" v-validate="'required'" placeholder="">
            <div class="fm_verifycode"><img src="../../assets/verify_code.jpg"></div>
            <div class="fm_errs">
              <span v-show="errors.has('verifycode')" class="help is-danger">{{ errors.first('verifycode') }}</span>
            </div>
          </div>
          <div class="fm_btns">
            <button type="submit"></button>
          </div>
          <div class="fm_bom_link">
            <a href="#/regist" class="first"></a>
            <a href="#/forget" class="last"></a>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data(){
      return {
        isShowAreas:false,
        username: '',
        password: '',
        verifycode: ''
      }
    },
    methods: {
      /*  */
      loginSubmit(){
        this.$validator.validateAll().then(() => {
          console.log('From Submitted!');
        }).catch(() => {
          console.log('Correct them errors!');
        });
      }
    }
  }
</script>
<style lang="scss">
  @import "../../styles/login/lyt_login_regist.css";
</style>



