import Vue from 'vue'
import zh_CN from 'vee-validate/dist/locale/zh_CN';
import VeeValidate, { Validator } from 'vee-validate';

/* locale */
Validator.addLocale(zh_CN);

/* Vue */
Vue.use(VeeValidate, {
  errorBagName: 'errors',
  delay: 0,
  locale: 'zh_CN',
  messages: null,
  strict: true
});

/*  */
const dictionary = {
  zh_CN: {
    messages: {
      required: ( field )=> "" + field
    },
    attributes:{
      username:'',
      password:'',
      verifycode: '',
      paypwd:'',
      realname: '',
      idcard: '',
      recoman: '',
      phone: '',
      address:'',
      detailaddr: '',
      withdrawcredit:'',
      bankaccount: '',
      bankname:'',
      banksubname:'',
      hascardname:'',
      oldpwd:'',
      newpwd: '',
      renewpwd: '',
      suggest: '',
      qq: 'QQ',
      email: ''
    }
  }
};
Validator.updateDictionary(dictionary);

/*  */
Validator.extend('idcard', {
  messages: {
    zh_CN:field => ''+field+'',
  },
  validate: value => {
    return /^(^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$)|(^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])((\d{4})|\d{3}[Xx])$)$/.test(value)
  }
});

Validator.extend('phone', {
  messages: {
    zh_CN:field => ''+field+'',
  },
  validate: value => {
    return value.length == 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/.test(value)
  }
});
Validator.extend('username', {
  messages: {
    zh_CN:field => '2-15'+field,
  },
  validate: value => {
    return /^[\u4E00-\u9FA5a-zA-Z0-9_]{2,15}$/.test(value)
  }
});







